.btn {
  background: #f5df36;
  background-image: -webkit-linear-gradient(top, #f5df36, #ff2200);
  background-image: -o-linear-gradient(top, #f5df36, #ff2200);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f5df36), to(#ff2200));
  background-image: linear-gradient(to bottom, #f5df36, #ff2200);
  border-radius: 28px;
  text-shadow: 0px 2px 1px #666666;
  font-family: Arial;
  color: #ffffff;
  font-size: 2.5rem;
  padding: 10px 20px 10px 20px;
  border: solid #14517a 0px;
  text-decoration: none;
  margin: 20px 20px;
  display: inline-block
  
}

.btn:active {
  background: #3cb0fd;
  background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3cb0fd), to(#3498db));
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
  cursor: pointer;
}

.btn:hover {cursor: pointer}


